/* Scrollbar Styling */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}
 
::-webkit-scrollbar-track {
    background-color: #ebebeb;
    -webkit-border-radius: 10px;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: $md-grey-400; 
}

*[contenteditable="true"]:focus{
/* 	outline: none; */
	outline-style: dashed;
	outline-color: rgb(220,220,220);
	outline-width: 2px;
	outline-offset: 5px;
}


.margin-icon{
	margin-right: 10px;
}

.status-icon{
	display: inline-block;
	width: 10px;
	height: 10px;
	border-radius: 20px;
	margin-right: 5px;
	
	&.status-icon-green{
		background-color: $default-success;
	}
	
	&.status-icon-red{
		background-color: $default-danger;
	}
	
	&.status-icon-orange{
		background-color: $default-warning;
	}
}

.text-subinfo{
	color: $md-grey-400;
	font-size: 12px;
	line-height: 18px;
}

.action-bar{
	.col-6:nth-child(2){
		text-align: right;
	}
}

.placeholder-avatar{
	position: relative;
	display: block;
	width: 2rem;
	height: 2rem;
	border-radius: 50px;
	background-color: $table-border-color;
	overflow: hidden;
	i{
		display: block;
		position: absolute;
		top: 65%;
		font-size: 27px !important;
		-webkit-transform: translateY(-50%); 
		-ms-transform: translateY(-50%);
		transform: translateY(-50%);
		right: 0;
		left: 0;
		margin:0 auto;
		color: $grey-500;
		text-align: center;
	}
}

#section-builder{
	&.hidden{
		display: none;
	}
}

#section-list{
	&.hidden{
		display: none;
	}
}

.peanut-jar{
	display: block;
	margin: 0 auto;
	width: 150px;
	height: 220px;
	position: relative;
	margin-top: 20px;
	
	@keyframes float {
		0% {
			transform: translatey(0px);
		}
		50% {
			transform: translatey(-20px);
		}
		100% {
			transform: translatey(0px);
		}
	}
	
	@keyframes floatShadow {
		0% {
			width: 100px;
			height: 100px;
			opacity: 0.6;
			-webkit-filter: blur(4px);
			-webkit-transform: scale(1, 0.2);
		}
		50% {
			width: 80px;
			height: 80px;
			bottom: 10px;
			opacity: 0.4;
			-webkit-filter: blur(10px);
			-webkit-transform: scale(1, 0.2);
			
		}
		100% {
			width: 100px;
			height: 100px;
			opacity: 0.6;
			-webkit-filter: blur(4px);
			-webkit-transform: scale(1, 0.2);
		}
	}
	
	img{
		position: relative;
		z-index: 1;
		display: block;
		margin: 0 auto;
		width: 100px;
		animation: float 6s ease-in-out infinite;
	}
	
	&:after{
		content: "";
		display: block;
		position: absolute;
		bottom:0px;
		left: 0;
		right: 0;
		margin: 0 auto;
		border-radius: 50%;
	    background: #bcbcbc;
	    animation: floatShadow 6s ease-in-out infinite;
	}
}


