.table-container{
	padding-left: 0 !important;
	padding-right: 0 !important;
}

table{
	tr{
		transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		
		&.head{
			background-color: rgba(0, 0, 0, 0.08) !important;
		}
		
		.handle{
			margin-right: 10px;
			cursor: pointer;
			opacity: 0.2;
			transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
			-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
			-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
			-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		}
		
		&:hover{
			.handle{
				opacity: 1;
			}
		}
	}
	
	.list-inline{
		text-align: right;
	}
}

.table-menu{
	tr:not(.head){
		
		.handle{
			margin-right: 10px;
		}
		
		&[data-level="1"] td:nth-child(1){
			a{
				margin-left: 15px;	
			}
		}
		
		&[data-level="2"] td:nth-child(1){
			a{
				margin-left: 30px;
				&:before{
					content: "- ";
				}	
			}
		}
		
		&[data-level="3"] td:nth-child(1){
			a{
				margin-left: 45px;
				&:before{
					content: "-- ";
				}	
			}
		}	
	}
}

.table-accordion{
	p{
		margin-bottom: 0;
	}
	
	.hidden{
		display: none;
	}
	
	b, .arrow{
		cursor: pointer;
	}
	
	.arrow{
		display: block;
		transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
		&.active{
			-moz-transform: rotate(-90deg);
			-webkit-transform: rotate(-90deg);
			-o-transform: rotate(-90deg);
			-ms-transform: rotate(-90deg);
			transform: rotate(-90deg);
		}
	}
	
	tr[data-level="2"] td:nth-child(1){
		p,b{
			margin-left: 30px;
		}
	}
	
	tr[data-level="3"] td:nth-child(1){
		p,b{
			margin-left: 45px;
		}
	}
}


