::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	color: $grey-500 !important;
}
::-moz-placeholder { /* Firefox 19+ */
	color: $grey-500 !important;
}
:-ms-input-placeholder { /* IE 10+ */
	color: $grey-500 !important;
}
:-moz-placeholder { /* Firefox 18- */
	color: $grey-500 !important;
}
::-ms-placeholder {
	color: $grey-500 !important;
}
::placeholder {
    color: $grey-500 !important;
}

form{
	button{
		width: 100%;
	}
}

.form-group{
	transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-webkit-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-moz-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	-o-transition:all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
	&.hidden{
		display: none !important;
	}
	
	&.transparant{
		opacity: 0.2 !important;
	}
	
	&.form-group-nm{
		margin-bottom: 0;
	}
}

.input-options{
	&.hidden{
		display: none;
	}
}

.input-switch {
  display: inline-block;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  i {
	  position: relative;
	  display: inline-block;
	  margin-right: .5rem;
	  width: 46px;
	  height: 26px;
	  background-color: $grey-300;
	  border-radius: 23px;
	  vertical-align: text-bottom;
	  transition: all 0.3s linear;
	  
	  	&:before {
		  content: "";
		  position: absolute;
		  left: 0;
		  width: 42px;
		  height: 22px;
		  background-color: $white;
		  border-radius: 11px;
		  transform: translate3d(2px, 2px, 0) scale3d(1, 1, 1);
		  transition: all 0.25s linear;
		}
		
		&:after {
		  content: "";
		  position: absolute;
		  left: 0;
		  width: 22px;
		  height: 22px;
		  background-color: $white;
		  border-radius: 11px;
		  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.24);
		  transform: translate3d(2px, 2px, 0);
		  transition: all 0.2s ease-in-out;
		}
	}
	
	p{
		display: inline-block;
		vertical-align: top;
	}
	
	input{
		display: none;
	}
}

.input-switch[type="checkbox"]:active i::after {
  width: 28px;
  transform: translate3d(2px, 2px, 0);
}

.input-switch:active input[type="checkbox"]:checked + i::after { transform: translate3d(16px, 2px, 0); }

.input-switch input[type="checkbox"]:checked + i { background-color: #4BD763; }

.input-switch input[type="checkbox"]:checked + i::before { transform: translate3d(18px, 2px, 0) scale3d(0, 0, 0); }

.input-switch input[type="checkbox"]:checked + i::after { transform: translate3d(22px, 2px, 0); }

.button-group{
	font-size: 0;
	.button-group-item{
		position: relative;
		font-size: 14px;
		display: inline-block;
    	padding: 0.75rem 1.25rem;
    	margin-right: -1px;
    	background-color: $white;
    	color: $grey-600;
		border:1px solid $table-border-color;
		
		&:first-child{
			border-top-left-radius: 5px;
			border-bottom-left-radius: 5px;
		}
		
		&:last-child{
		    border-top-right-radius: 5px;
			border-bottom-right-radius: 5px;
		}
		
		&.active{
			color:$grey-600;
			background-color: $table-border-color;
		}
	}
}

.list-group-status{
	a[data-status="1"].active{
		color: #fff;
		background-color: $default-success;
		border-color: $default-success;
	}
	a[data-status="0"].active{
		color: #fff;
		background-color: $default-danger;
		border-color: $default-danger;
	}
}

.slug-input{
	font-weight: 600;
	color: $grey-600 !important;
	text-transform: uppercase;
}


